import React from 'react';
import { Helmet } from 'react-helmet-async';

const SEOHead = ({ 
  title,
  description,
  keywords,
  image,
  url,
  type = 'website',
  structuredData,
  noIndex = false
}) => {
  const siteUrl = process.env.REACT_APP_SITE_URL || 'https://datainamics.github.io';
  const siteName = process.env.REACT_APP_SITE_NAME || 'datainamics';
  
  const fullTitle = title ? `${title} | ${siteName}` : `${siteName} - Applied AI Research Lab`;
  const fullUrl = url ? `${siteUrl}${url}` : siteUrl;
  const defaultDescription = 'Applied AI Research Lab based in Morocco, bridging cutting-edge AI research to deployable solutions with global impact and Africa-first mindset.';
  const defaultKeywords = 'AI, Artificial Intelligence, Research, Morocco, Africa, Machine Learning, AI Training, Technology, Applied AI';
  const defaultImage = `${siteUrl}/favicon.svg`;

  return (
    <Helmet>
      {/* Basic Meta Tags */}
      <title>{fullTitle}</title>
      <meta name="description" content={description || defaultDescription} />
      <meta name="keywords" content={keywords || defaultKeywords} />
      <link rel="canonical" href={fullUrl} />
      
      {/* Open Graph / Facebook */}
      <meta property="og:type" content={type} />
      <meta property="og:title" content={fullTitle} />
      <meta property="og:description" content={description || defaultDescription} />
      <meta property="og:image" content={image || defaultImage} />
      <meta property="og:url" content={fullUrl} />
      <meta property="og:site_name" content={siteName} />
      
      {/* Twitter */}
      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:title" content={fullTitle} />
      <meta name="twitter:description" content={description || defaultDescription} />
      <meta name="twitter:image" content={image || defaultImage} />
      
      {/* Additional Meta Tags */}
      <meta name="author" content="datainamics" />
      <meta name="robots" content={noIndex ? 'noindex,nofollow' : 'index,follow'} />
      <meta name="language" content="English" />
      <meta name="revisit-after" content="7 days" />
      
      {/* Structured Data */}
      {structuredData && (
        <script type="application/ld+json">
          {JSON.stringify(structuredData)}
        </script>
      )}
    </Helmet>
  );
};

export default SEOHead;
